import React from 'react'
import { Element } from 'react-scroll';
import Project from '../Project/Project';
import './ProjectConatiner.css'

//import calc from '../TopContent/calcuout2.png';

const ProjectConatiner = () => {
    
    const projects = [
        {
            img:'https://www.involve.me/assets/images/blog/how-to-create-a-simple-price-calculator-and-capture-more-leads/calculator-M.png',
            title:"Calculator",
            desc: "Calclator Build Using React JS",
            link:"https://github.com/sathish0704/Calculator.git"
        },
        {
            img:"https://i.ibb.co/MCFX10J/Weatheroutput2.png",
            title:"Weather Report",
            desc: "Build Using React JS & openweather API",
            link:"https://github.com/sathish0704/weatherapp.git"
        },
        {
            img:"https://cdn.wallpapersafari.com/12/29/UQhMIt.jpg",
            title:"Calculator",
            desc: "Calclator Build Using React JS",
            link:"www.google.com"
        },
        {
            img:"https://cdn.wallpapersafari.com/12/29/UQhMIt.jpg",
            title:"Calculator",
            desc: "Calclator Build Using React JS",
            link:"www.google.com"
        },
        {
            img:"https://cdn.wallpapersafari.com/12/29/UQhMIt.jpg",
            title:"Calculator",
            desc: "Calclator Build Using React JS",
            link:"www.google.com"
        },
        {
            img:"https://cdn.wallpapersafari.com/12/29/UQhMIt.jpg",
            title:"Weather APP",
            desc: "Weather APP Build Using React JS",
            link:"www.google.com"
        },
    ];

  return (
    <Element className='projectConatainer' id='projects'>
        <h1>Projects</h1>
        <p>Here are some projects which i have done from my end</p>
        <div className='projectContainer__projects'>
        {
            projects.map((project, index) =>
            {
                return(
                        <Project key={index} img={project.img} title={project.title} desc={project.desc} link={project.link}/>
                      )
            })
        }   
        </div>
    </Element>      
  )
}
export default ProjectConatiner
