import React from 'react'
import { Link } from 'react-scroll'
import './TopContent.css';
import resume from '../TopContent/SATHISH_KUMAR_S.pdf';

const TopContent = () => {
  return (
    <div className='topContent'>
        <div className='topContent_conatiner'>
            <h1>Mr.<span>Sathish</span> Kumar</h1>
            <p>Full Stack <span id='devlop'>Developer</span></p><br></br>
            <a href='google.com'>
            <a href={resume} download="Sathish Resume" target='_blank'>
                <button className='topContent_downloadbutton'>Download CV</button>
                </a>
                <Link to='projects' smooth='true' duration={500}>
                    <button className='topContent_workbutton'>MY Work</button>
                </Link>
            </a>
        </div>
    </div>
  )
}

export default TopContent
